	module.exports = {"data":{"post":{"html":"<p>For 3 weeks now we've been hearing about the tons <em>Gammelfleisch</em>, or putrid meat, being found in the meat lockers of Germany, and how long the authorities knew about it before it was made public. The best comment I saw on it was at <a href=\"http://www.eamonn.com/2006/09/somethings_rotten_in_the_state.htm\">Rainy Day</a>, which cited The Jungle, the classic American exposé of conditions in the Chicago stockyards in 1906. Noone has died or even gotten sick from bad meat in the current German scandal, although that's hard to tell from the sensationalist press coverage.</p>\n<p>Meanwhile in the States, fresh bagged spinach tainted with <em>E. coli</em> has <a href=\"http://www.cnn.com/2006/HEALTH/09/16/tainted.spinach.ap/index.html\">killed one and sent nearly 100 to the hospital</a> in several states over the past few weeks, including several brands of organic spinach. So, my German friends, don't think you're any better off if you skip meat and eat only vegetables from the <em>Bioladen</em>. You're taking chances no matter what you eat.</p>\n<p>One of the things I first noticed here was the rancid smell in some (but not all) German butcher shops. I used to clean a meat market as an after-school job, so I know what a clean meat market looks and smells like. I still feel more comfortable with meat from the supermarket than from a butcher who may have different ideas of cooling and sanitation than I'm used to.</p>","fields":{"title":"Gammelgemuse","date":"16 Sep 2006","url":"https://papascott.de/2006-09-16-gammelgemuse/"}}},"pathContext":{"slug":"/2006-09-16-gammelgemuse/","prev":{"fields":{"title":"Han Shot First. Really.","date":"14 Sep 2006","slug":"/2006-09-14-han-shot-first-really/"}},"next":{"fields":{"title":"How to keep your cell phone from disrupting family time","date":"16 Sep 2006","slug":"/2006-09-16-how-to-keep-your-cell-phone-from-disrupting-family-time/"}}}}

